.App {
  text-align: center;
}

.streamInfo {
  display: flex;
  justify-content: space-around;
}

body, #page {
  background-color: #121212;
}

#nav {
  display: flex;
  justify-content: center;
  text-align: center;
}

.title {
  font-size: 2rem;
}

.playing img {
  max-width: 100%;
}

.live-channel {
  background-color: #1C1C1C;
  display: flex;
  padding: 1em;
  margin-bottom: 1em;
  margin-top: 1em;
  justify-content: center;
}

.streamer {
  width: 50%;
  display: flex;
  flex-direction:  column;
  padding-right: 1em;
  border-right: 1px solid #e0e0e0;
}

.playing {
  width: 50%;
  display: flex;
  padding-left: 1em;
  flex-direction:  column;
}

.profile-pic {
  width: 100px;
}

.description {
  background-color: #222;
}

strong {
  color: #a44;
}